<template>
  <div>
    <div class="bg-red-400 d-flex d-flex justify-content-center shadow-lg rounded-lg" id="header" style="height:10vh;100vw;" >
        <div class="align-items-center">
            <p class="p-4 font-bold">
                LIVE UPDATE DATA COVID 19 DI INDONESIA
            </p>
        </div>
    </div>
      <div id="content" style="min-height:80vh;100vw;">
          <div class="container">
              <div class="row justify-content-center">
                <div class="col-10">
                  <p class="p-4">
                    Terakhir Update : {{new Date(dataTerakhir.tanggal).toString()}}
                  </p>
                </div>
                  <div class="col-3 text-center font-bold ">
                      POSITIF
                      <p class="text-2xl">
                          {{dataTerakhir.positif}}
                      </p>
                  </div>
                  <div class="col-3 text-center font-bold">
                      DIRAWAT
                      <p class="text-2xl">
                      {{dataTerakhir.dirawat}}

                      </p>
                  </div>
                  <div class="col-3 text-center font-bold">
                      SEMBUH
                      <p class="text-2xl">
                      {{dataTerakhir.sembuh}}
                      </p>
                  </div>
                  <div class="col-3 text-center font-bold">
                      MENINGGAL
                      <p class="text-2xl">
                      {{dataTerakhir.meninggal}}
                      </p>
                  </div>
                  <div class="col-10 p-4">
                    <div v-if="ready">
                      <apexchart type="line" height="350" :options="chartOptions" :series="series"></apexchart>
                    </div>
                    <div v-else>
                      <p class="text-center p-5 animated bounce infinite">
                        LOADING ...
                      </p>
                    </div>
                  </div>
                   <div class="col-10 p-4">
                     <hr class="style13">
                     <p class="p-3 text-lg">Data Covid Per Provinsi : </p>
                     <select class='form-control' v-model="selected">
                     <option v-for="(item, index) in listProv" :key="index+'listprov'">{{item}}</option>
                     </select>
                  </div>
                  <div class="col-3 text-center font-bold ">
                      POSITIF
                      <p class="text-2xl" v-if="dataProvs.length>0">
                          {{dataProvs[0].kasus}}
                      </p>
                  </div>
                  
                  <div class="col-3 text-center font-bold ">
                      DIRAWAT
                      <p class="text-2xl" v-if="dataProvs.length>0">
                          {{dataProvs[0].dirawat}}
                      </p>
                  </div>
                  
                  <div class="col-3 text-center font-bold ">
                      SEMBUH
                      <p class="text-2xl" v-if="dataProvs.length>0">
                          {{dataProvs[0].sembuh}}
                      </p>
                  </div>
                  
                  <div class="col-3 text-center font-bold ">
                      MENINGGAL
                      <p class="text-2xl" v-if="dataProvs.length>0">
                          {{dataProvs[0].meninggal}}
                      </p>
                  </div>
                   <div class="col-10 p-4">
                     <hr class="style13">
                     <p class="p-3 text-lg">Data Vaksin Se Indonesia: </p>
                     <p>Terakhir Update : {{new Date(dataVaksin.lastUpdate).toString()}}</p>
                  </div>
                    <div class="col-3 text-center font-bold ">
                      Total Sasaran
                      <p class="text-2xl" >
                          {{dataVaksin.totalsasaran}}
                      </p>
                  </div>
                   <div class="col-3 text-center font-bold ">
                      Total Sasaran Vaksin DMK
                      <p class="text-2xl" >
                          {{dataVaksin.sasaranvaksinsdmk}}
                      </p>
                  </div>
                 
                    <div class="col-3 text-center font-bold ">
                      VAKSINASI 1
                      <p class="text-2xl" >
                          {{dataVaksin.vaksinasi1}}
                      </p>
                  </div>
                    <div class="col-3 text-center font-bold ">
                      VAKSINASI 2
                      <p class="text-2xl" >
                          {{dataVaksin.vaksinasi2}}
                      </p>
                  </div>
                  <div class="col-12">
                    <br>
                    <br>
                    <br>
                    <br>
                  </div>
              </div>
          </div>
      </div>
      <div id="footer" style="height:10vh;100vw;background:black;color:white;">
          <p class="text-center p-5">
              Data diambil dari api : Data COVID-19 di Indonesia by Reynaldi
          </p>
      </div>
  </div>
</template>
<script>
import axios from 'axios'
import apexchart from 'vue-apexcharts'
export default{
    components:{
        apexchart
    },
  data(){
    return{
        datanya:[],
        dataTerakhir:[],
        dataProv:[],
        listProv:[],
        dataVaksin:[],
        selected:'KALIMANTAN SELATAN',
        ready:false,
          series: [{
              name: "Positif",
              data: [10, 41, 35, 51, 49, 62, 69, 91, 148]
          },{
              name: "Sembuh",
              data: [10, 41, 35, 51, 49, 62, 69, 91, 148]
          },{
              name: "Dirawat",
              data: [10, 41, 35, 51, 49, 62, 69, 91, 148]
          },{
              name: "Meninggal",
              data: [10, 41, 35, 51, 49, 62, 69, 91, 148]
          }
          ],
          chartOptions: {
            chart: {
              height: 350,
              type: 'line',
              zoom: {
                enabled: false
              }
            },
            dataLabels: {
              enabled: false
            },
            stroke: {
              curve: 'straight'
            },
            title: {
              text: 'Covid 19 di Indonesia 1 Bulan Terakhir',
              align: 'left'
            },
            grid: {
              row: {
                colors: ['#f3f3f3', 'transparent'], // takes an array which will be repeated on columns
                opacity: 0.5
              },
            },
            xaxis: {
              categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep'],
            }
          },
    }
  },
  computed: {
    dataProvs(){
      let data =this.dataProv;
      data=data.filter(e=>{
        if(e.provinsi==this.selected){
          return e
        }
      })
      return data;
    }
  },
  methods:{
    getDataVaksin(){
      axios.get('https://vaksincovid19-api.vercel.app/api/vaksin').then(res=>{
        let data = res.data
        this.dataVaksin=data
      })
    },
      getDataProv(){
        axios.get('https://apicovid19indonesia-v2.vercel.app/api/indonesia/provinsi').then(res=>{
          let data = res.data;
          this.dataProv=data;
          this.listProv=data.map(e=>{
            return e.provinsi
          })
        })
      },
      getData(){
       axios.get('https://apicovid19indonesia-v2.vercel.app/api/indonesia/harian').then((response) => {
        let data = response.data
        this.datanya =data;
        this.dataTerakhir=data[data.length-1];
        console.log(data[data.length-1])
        data = data.slice(data.length-30,data.length)
        console.log(data);
        let kategori = data.map(e=>{
          let tgl=new Date(e.tanggal).toString()
          return tgl.split(" ")[1]+"-"+tgl.split(" ")[2];
        })
        let positif = data.map(e=>{
          return e.positif;
        })
        let dirawat = data.map(e=>{
          return e.dirawat;
        })
        let meninggal = data.map(e=>{
          return e.meninggal;
        })
        let sembuh = data.map(e=>{
          return e.sembuh;
        })
        console.log(positif);
        this.chartOptions.xaxis.categories=kategori
        this.series[0].data=positif;
        this.series[1].data=sembuh;
        this.series[2].data=dirawat;
        this.series[3].data=meninggal;
        setTimeout(() => {
          this.ready=true;
        }, 2000);
        this.$forceUpdate();
        }).catch((e) => {
            console.log(e);
        });
      }
  },
  mounted() {
    this.getData()
    this.getDataProv();
    this.getDataVaksin();
  },
}
</script>